.participants{
    width: 80%;
    position: absolute;
    max-width: 600px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 1.5em;
    background: rgba(255,255,255,1);
}

.dark-participants{
    /*border: 025px solid rgba(50, 50, 50, 0.9);*/
    background: rgba(50,50,50,1);
    
}

.dark-participants *{
    color: rgba(255,255,255,1);
}

.participants h2{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.parTable{
    position: relative;
    width: 100%;
    border-radius: 1em;
}

.parTable .table{
    border-radius: 1em;
    padding: 0;
    margin: 0;
}

.parTable .thead, .parTable .tbody{
    padding-left: 0;
    padding-right: 0;
}
.parTable .tr{
    margin-left: 0;
    margin-right: 0;
    margin-left: .6rem;
    margin-right: .6rem;
}

.parTable .th, .parTable .td {
    text-align: center;
    padding: 0;
    /*border: 1px solid red;*/
}

.parTable .th{
    font-weight: 600;
}

.table{
    overflow: hidden;
}

.tbody {
    position: relative;
    padding: 0 1.5em;
}

.tbody .tr{
    border-radius: .4rem;
}

/*.tbody .tr:nth-child(odd){
    background: rgba(0,0,0,0.1);
}*/

.tbody .tr:hover{
    background-image: linear-gradient(to right bottom, red,orange,yellow);;
}

/* width */
.tbody::-webkit-scrollbar {
    width: .5em;
}
  
  /* Track */
.tbody::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 1em;
}
   
  /* Handle */
.tbody::-webkit-scrollbar-thumb {
    background: rgba(150,150,150, .5); 
    border-radius: 1em;
}
  
  /* Handle on hover */
.tbody::-webkit-scrollbar-thumb:hover {
    background: rgba(150,150,150, 1); 
}

.deletePar{
    position: absolute;
    right: 10%;
    top: 50%;
    transform: translate(0,-50%);
    display: none;
}

.participant:hover .deletePar{
    display: block;
}

.addPar{
    position: absolute;
    bottom: 3%;
}

.addPar .input{
    background: transparent;
    border: 0;
    border-bottom: 1px;
}

@media only screen and (max-width: 576px) {
    /* width */
    .tbody::-webkit-scrollbar {
        width: 0;
    }

    .participant:hover .deletePar{
        display: none;
    }
}